@import url(//db.onlinewebfonts.com/c/29dc27977e417a98e56556776f41607c?family=Corbel);
/* @import '../node_modules/jquery/dist/jquery.js'; */
body {
  margin: 0;
  overflow: hidden;
  font-family: corbel; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

iframe {
  width: 100%; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.sidenav {
  width: 4%;
  -webkit-transition: all 1s ease-in-out 0s;
  transition: all 1s ease-in-out 0s; }
  .sidenav a span {
    opacity: 0;
    -webkit-transition: all 1s ease-in-out 0s;
    transition: all 1s ease-in-out 0s; }
  .sidenav:hover {
    width: 18%;
    -webkit-transition: all 1s ease-in-out 0s;
    transition: all 1s ease-in-out 0s; }
    .sidenav:hover a span {
      opacity: 1;
      -webkit-transition: all 1s ease-in-out 0s;
      transition: all 1s ease-in-out 0s; }

.full-width-container {
  width: 96%; }

.top-header {
  background-color: #11b7e6;
  height: 67px; }

.h-line-height {
  line-height: 67px; }

.h-color {
  color: #fff; }

.bold {
  font-weight: bold; }

.main-content {
  height: 84vh;
  overflow-y: scroll; }

.logout {
  color: #fff;
  cursor: pointer;
  margin: 0 15px; }

nav.menu ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

nav.menu ul li {
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #0c90b5;
  min-width: 250px; }

nav.menu ul li a {
  padding: 12px 0;
  display: flex;
  align-items: center;
  text-decoration: none;
  border-left: 2px solid transparent; }
  nav.menu ul li a:hover, nav.menu ul li a.active {
    border-left: 2px solid #f59f03;
    background-color: #0c90b5; }

nav.menu ul li a i {
  font-size: 20px;
  line-height: 1.5rem;
  min-width: 1.25rem;
  color: #fff;
  position: relative;
  left: 0;
  margin: 0 15px; }

nav.menu ul li a span {
  color: #fff; }

.logo-section {
  height: 62px;
  margin-bottom: 5px; }
  .logo-section a {
    line-height: 67px; }

.img {
  margin-left: 20%;
  width: 60%;
  height: auto; }

.menu-list {
  background-color: #11b7e6;
  overflow: hidden; }

.menu {
  height: 100%;
  background-color: #11b7e6; }

nav.menu ul li a:hover {
  text-decoration: none;
  background-color: #0c90b5; }

.Project-info {
  margin-top: 10px;
  /* text-align: center;
    position: relative;
    float:left;  */
  margin-left: 10px; }

.label {
  font-weight: bold;
  margin-top: 10px; }

.projectClass {
  margin-top: 20px;
  font-weight: bold; }

.submit-button {
  width: 125px;
  margin-top: 10px;
  background-color: #11b7e6;
  color: #fff; }

.pb-height > .progress {
  height: 30px; }

.dropdown-toggle:after {
  margin-top: 10px; }

.multi-select-list {
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 130px;
  margin: 0;
  padding: 0; }

.dropdown-item:hover {
  background-color: #3593f9; }

.check-box {
  opacity: 0; }

.form-control-custom {
  width: 100%;
  min-height: calc(1.5em + .75rem + 2px);
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out; }

.item-chip {
  background-color: #eaecef;
  border-radius: 15px;
  padding: 1px 7px; }

.top-heading {
  margin: 0 20px;
  line-height: 48px;
  border-bottom: 1px solid #ced4da; }

.info-header {
  background-color: #11b7e6;
  color: #fff; }

.build-info {
  background-color: #e9ecef;
  margin: 20px; }

.text-success {
