  font-size: 16px;
  padding: 5px 10px;
  margin: 10px;
  background-color: #9ca4ab; }
  .view-logs:hover {
    background-color: #90979e; }

.content {
  max-height: 610px;
  overflow: scroll; }

.modal-head {
  background-color: #11b7e6;
  color: #fff; }

.footer {
  height: 50px;
  background-color: #8f9192;
